.modal {
  width: 31.25rem;
  padding: 2.50rem 2.1rem;
  height: auto;
}

.modal__inner {
  width: 100%;
  height: 100%;
}

.modal__title {
  margin-bottom: 3rem;
  font-weight: 700;
  font-size: 2rem;
  text-transform: uppercase;
  text-align: center;
}

.modal__text {
  margin-bottom: 3rem;
  text-align: center;
}

.modal-alert {
  width: 55rem;
  padding: 5rem;
  height: auto;
}

.modal-alert__inner {
  width: 100%;
  height: 100%;
}

.modal-alert__pic {
  display: block;
  margin: 0 auto 3rem;
}

.modal-alert__pic-complaint {
  width: 10rem;
}

.modal-alert__pic-send {
  width: 10rem;
}

.modal-alert__buttons {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem
}

.modal-alert__button {
  padding: 1.3rem 3rem;
}

.modal-alert__button-send {
  padding: 1.3rem 10rem;
}

.modal-share {
  width: 50rem;
  padding: 4rem;
}

.modal-share .modal__title {
  font-size: 2rem;
}

.modal-share__social {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 2rem;
  margin-bottom: 1.88rem;
}

.modal-share__social a {
  --size: 4rem;
  width: var(--size);
  height: var(--size);
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  transition: 0.4s;
}

.modal-share__social a:hover {
  opacity: 0.7;
}

.modal-share__social-facebook {
  background: #446294;
}

.modal-share__social-twitter {
  background: #40ABE2;
}

.modal-share__social-vk {
  background: #0077FF;
}

.modal-share__social img,
.modal-share__social svg {
  width: 2.6rem;
}

.modal-share__link-text {
  margin-bottom: 0.5rem;
}

.modal-share__input {
  width: 100%;
  padding-top: 1.2rem;
  padding-bottom: 1.2rem;
  padding-left: 1.25rem;
  border: 1px solid #B4B4BF;
  border-radius: var(--brad-regular);
  background: #fff;
  margin-bottom: 1.88rem;
}

.modal-share__button {
  padding: 1.3rem 3rem;
}